"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Task } from "@/src/types";
import { editTodo } from '@/src/api';

interface TodoProps {
  todo: Task;
  setTodos: React.Dispatch<React.SetStateAction<Task[]>>; // setTodos を props として受け取る
}

const Todo = ({ todo, setTodos }: TodoProps) => {
  const ref = useRef<HTMLInputElement>(null); 

  const [isEditing, setIsEditing] = useState(false);
  const [editedTaskTitle, setEditedTaskTitle] = useState(todo.text);

  useEffect(() => {
    if (isEditing) {
      ref.current?.focus();
    }
  }, [isEditing]);

  const handleEdit = async () => {
    setIsEditing(true);
  };

  const handleSave = async () => {
    try {
      const updatedTodo = await editTodo(todo.id, editedTaskTitle);
      
      // タスクが更新されたので、ローカルの todos リストを更新
      setTodos((prevTodos) =>
        prevTodos.map((task) =>
          task.id === updatedTodo.id ? updatedTodo : task // 変更されたタスクを更新
        )
      );
      
      setIsEditing(false);
    } catch (error) {
      console.error("Failed to save todo:", error);
    }
  };

  return (
    <li key={todo.id} className='flex justify-between p-4 bg-white border-l-4 border-blue-500 rounded shadow'>
      {isEditing ? (
        <input
          ref={ref}
          type="text"
          className="mr-2 py-1 px-2 rounded border-gray-400 border"
          value={editedTaskTitle}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setEditedTaskTitle(e.target.value)}
        />
      ) : (
        <span>{todo.text}</span>
      )}

      <div>
        {isEditing ? (
          <button className='text-blue-500 mr-3' onClick={handleSave}>
            save
          </button>
        ) : (
          <button className='text-green-500 mr-3' onClick={handleEdit}>
            edit
          </button>
        )}

        <button className='text-red-500'>Delete</button>
      </div>
    </li>
  );
};

export default Todo;
